import React, { Suspense } from "react";
const Author = React.lazy(() => import("../../../Elements/Author/Author"));
const Time = React.lazy(() => import("../../../Elements/Time/Time"));
const Network = React.lazy(() => import("../../../Elements/Network/Network"));

const AuthorDetails = ({ author, personalization, postTime, network, ThemeRule, font }) => {
  const username = author.username ? author.username.length > 0 ? `@${author.username}` : `` : '';
  const postAuthor = (personalization.postAuthor && !author.isInstaUser) ? true : false;
  const authorSeprator = (personalization.postAuthor && personalization.postTime && !author.isInstaUser) ? true : false;
  const socialIcon = ThemeRule.socialAction ? true : false;
  return <div className="tb_flux_author_wrapper">
    <div className="tb_flux_author">
      {/* {postAuthor ? <Suspense fallback={<></>}><Author author={author} network={network} authorClass={'tb_flux_author_profile'} /></Suspense> : ''} */}
      <div className="tb_flux_author_info">
        {/* {postAuthor ? <div className="tb_flux_authorname">{author.name}</div> : ''} */}
        <div className="tb_flux_post_info">
          {/* {postAuthor ? <div className="tb_flux_username">{username}</div> : ''}
          {authorSeprator ? <div className="tb_flux_seprator"> </div> : ''} */}
          {personalization.postTime ? <Suspense fallback={<></>}><Time postTime={postTime} timeClass={`tb_flux_time tb-cTBfont-${ThemeRule.font_varient}`} authorColor={ThemeRule.authorColor} /> </Suspense> : ''}
        </div>
      </div>
    </div>
    {/* {socialIcon ?
      <div className="tb_flux_social_">
        <Suspense fallback={<></>}><Network networkClass={'tb_flux_social__ico'} network={network} isDefault={ThemeRule.iconType === 1 ? true : false} ThemeRule={ThemeRule} /></Suspense>
      </div> : null} */}
  </div>
}
export default AuthorDetails